
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,LoadingController } from 'ionic-angular';
import { CommonProvider } from '../../providers/common';
import { JoinschemePage } from '../joinscheme/joinscheme';
import { SchemedetailPage } from '../schemedetail/schemedetail';
import { SchemetypePage } from '../schemetype/schemetype';
/**
 * Generated class for the MyschemePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-closed',
  templateUrl: 'closed.html',
  providers: [CommonProvider]

})
export class ClosedPage {

  close:any[] = [];
  currency:any = '';
  id_branch:any =null;



  constructor(public load:LoadingController, public comman:CommonProvider,public navCtrl: NavController, public navParams: NavParams) {
    let loader = this.load.create({
      content: 'Please Wait',
      spinner: 'dots',
    });
    loader.present();

    this.comman.getcurrency(this.id_branch).then(data =>{

      this.currency = data;
      
    this.comman.closed().then(data=>{

      this.close = data['closed_acc'];
      console.log(this.close);
      loader.dismiss();
      
    })
  })

  }
 


  ionViewDidLoad() {
    console.log('ionViewDidLoad MyschemePage');
  }
  gotype(){

    this.navCtrl.setRoot(SchemetypePage)
  }
}



